<script>
    import Card from '../components/Card.svelte'    
    import X from 'jquery'
    import { onMount } from 'svelte'
    export let body
    export let head
    export let trigger
    
    onMount(()=> {

            X('.notifikasi').delay(600).slideDown(200);
            X('.notifikasi').delay(2000).slideUp(600,function(){
                trigger = " "
            });
        
    })

</script>



<Card style="display: none; position: absolute; right: 16px; top: 16px; background-color: white; width: 200px; height: 75px;" Class="notifikasi" useShadow>
    <p>{head} </p>
    <p class="status-notifikasi">{body}</p>
</Card>